*{
  margin: 0%;
  padding: 0%;
  
}
body{
  background-color: gainsboro;
  font-family: 'Roboto', sans-serif;
}
:root{
  font-size: 62.5%;
 
  --fs-heading-sm: clamp(2rem, 2rem + 3vw, 3rem);
  --fs-heading-s: clamp(1rem, 0.5rem + 3vw, 2rem);
  --fs-heading-b: clamp(1rem, 0.5rem + 3vw, 1.6rem)
}


.conteiner{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  
} 
.conteiner .cabesalho{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  
  
  background-color: black;
  height: 100%;
  width: 100%;
  padding: 1rem;
  color: white;
  margin-bottom: 2.0rem;

}
.cabesalho h1{
  font-size: var(--fs-heading-sm);
}

.corpo{
  display: flex;
  flex-direction: column;
  
  background-color:white;
  max-width: 100%;
  width: 50rem;
}
.corpo h2{
  font-size: var(--fs-heading-sm) ;
  font-weight: 700;
  margin-bottom: 20px;
  padding:0 20px;
  text-align: center;
}
.input{
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  padding-inline: 10px;
}
.corpo input{
 
  padding-inline: 5px;
  width: 300px;
  height: 30px;
  border-radius: 4px;
  font-size: var(--fs-heading-s);
  cursor: pointer;
  font-weight: 500;

}
.button{
  display: flex;
  justify-content: space-evenly;
}
.corpo button{
  
  font-size: var(--fs-heading-b);
 
  padding: 0.6rem 3rem ;
  margin-bottom: 20px;
  border: 0.5px solid black;
  border-radius: 8px;
  cursor: pointer;
  transition: 300ms;
}
button:hover{
  background-color: #116efb;
    color: #fff;
    border: none;
}
.tempo{
  display: flex;
  align-items: center;
  justify-content: center;
  
}
.temp h3{
  margin: 20px;
  font-weight: 700;
}
.temp p{
  margin: 20px;
}

.temp article{
  display: flex;
  float: left;
  
  margin: 25px;
}
.temp div{
  border-bottom: 1px solid black;
  width: 200px;
  
}